<template>
    <div id="problemset">
        <loadings ref="load"></loadings>
        <!--<navbar></navbar>-->
        <navbar :isActive="page_belong"></navbar>
        <div class="container mundb-standard-container" style="min-height: 70vh">
            <div class="row">
                <div class="col-sm-12 col-lg-9">
                    <paper-card class="animated bounceInLeft" style="min-height: 292px">
                        <div class="table-responsive">
                            <table id="problems" class="table table-borderless">
                                <thead>
                                <tr>
                                    <th scope="col" style="width: 10%">
                                        <form class="form-inline"
                                              style="margin-bottom: 0px" @submit.prevent="choose_problem_id()">
                                            <input v-model="search.problem_id" class="form-control search-query"
                                                   type="text" style="width: 100%;text-align: center"
                                                   name="pid" placeholder="ID">
                                        </form>
                                    </th>
                                    <th scope="col">
                                        <form class="form-inline" style="margin-bottom: 0px"
                                              @submit.prevent="choose_title()">
                                            <input v-model="search.title" type="text"
                                                   style="width:100%;text-align: center"
                                                   class="form-control search-query"
                                                   placeholder="Problems">
                                        </form>
                                    </th>
                                    <th scope="col" class="cm-fw" style="padding-bottom: 20px;">
                                        Solved
                                    </th>
                                    <th scope="col" class="cm-fw" style="padding-bottom: 20px;">
                                        Submit
                                    </th>
                                    <th scope="col" class="cm-fw" style="padding-bottom: 20px;">
                                        AC rate
                                    </th>
                                </tr>
                                </thead>
                                <tbody>
                                <tr v-for="saved_problem in problems" v-bind:key="key">
                                    <th scope="row"> CTGAOJ{{saved_problem.problemId}}</th>
                                    <td>
                                        <template v-if="saved_problem.hasPass == 0">
                                            <i style="color: lightgrey" class="fa fa-circle-o" aria-hidden="true"></i>
                                        </template>
                                        <template v-else-if="saved_problem.hasPass == 2">
                                            <i style="color: orange" class="fa fa-adjust" aria-hidden="true"></i>
                                        </template>
                                        <template v-else>
                                            <i style="color: lightseagreen;" class="fa fa-circle"
                                               aria-hidden="true"></i>
                                        </template>

                                        <span>{{" "}}</span>
                                        <a style="text-decoration:none;"
                                           v-bind:href="'problem.html?pid='+ saved_problem.problemId">{{saved_problem.title}}</a>
                                    </td>
                                    <td> {{saved_problem.submitStatistics.allSubmitTimes}}</td>
                                    <td> {{saved_problem.submitStatistics.acSubmitTimes}}</td>
                                    <td> {{saved_problem.submitStatistics.allSubmitTimes == 0? "---":
                                        parseFloat(saved_problem.submitStatistics.acSubmitTimes*100/saved_problem.submitStatistics.allSubmitTimes).toFixed(2)+"%"}}
                                    </td>
                                </tr>
                                </tbody>
                            </table>
                            <empty-container v-if="problems != null && problems.length <= 0">
                                <svg t="1605936727818" class="icon" viewBox="0 0 1024 1024" version="1.1"
                                     xmlns="http://www.w3.org/2000/svg" p-id="3621" width="100" height="100">
                                    <path d="M576 928c-6 0-11.9-1.7-17.1-5-9.3-5.9-14.9-16.1-14.9-27V554.3c0-12.4 7.1-23.6 18.3-28.9l288-136.1c9.9-4.7 21.5-4 30.8 1.9s14.9 16.1 14.9 27v341.7c0 12.4-7.1 23.6-18.3 28.9l-288 136.1c-4.4 2.1-9 3.1-13.7 3.1z m32-353.5v271l224-105.8v-271L608 574.5zM448 928c-4.7 0-9.3-1-13.7-3.1l-288-136.1c-11.2-5.3-18.3-16.6-18.3-28.9V418.2c0-11 5.6-21.2 14.9-27 9.3-5.9 20.9-6.6 30.8-1.9l288 136.1c11.2 5.3 18.3 16.6 18.3 28.9V896c0 11-5.6 21.2-14.9 27-5.2 3.3-11.1 5-17.1 5zM192 739.7l224 105.8v-271L192 468.7v271zM512 501.1c-4.7 0-9.4-1-13.8-3.1l-352-168.1c-11.2-5.4-18.3-16.7-18.2-29.2 0.1-12.4 7.4-23.7 18.8-28.8l352-159.9c8.4-3.8 18.1-3.8 26.5 0l352 159.9c11.3 5.1 18.6 16.4 18.8 28.8 0.1 12.4-7 23.8-18.2 29.2L525.8 498c-4.4 2-9.1 3.1-13.8 3.1zM235.8 301.7L512 433.6l276.2-131.9L512 176.2 235.8 301.7z"
                                          p-id="3622" fill="#7a8e97"></path>
                                </svg>
                                <p>Nothing matches.</p>
                            </empty-container>
                        </div>
                    </paper-card>
                    <nav aria-label="..." class="animated bounceInLeft" style="min-height: 36px">
                        <ul class="pagination justify-content-center">
                            <li v-bind:class="{'page-item':true, 'disable': page <= 1}">
                                <a class="page-link cm-navi"
                                   v-bind:href="page > 1? switch_page(page-1): 'javascript:void(0)'">
                                    <i style="line-height: 18px;" v-bind:style="{'color': page <= 1? 'grey': ''}"
                                       class="material-icons prefix">chevron_left</i>
                                </a>
                            </li>
                            <template v-if="max_page <= 14">
                                <li class="page-item" v-for="i in page_index_range(1, page-1)" v-bind:key="key">
                                    <a class="page-link" v-bind:href="switch_page(i)">{{i}}</a>
                                </li>
                                <li class="page-item active" aria-current="page">
                                    <span class="page-link">{{page}}</span>
                                </li>
                                <li class="page-item" v-for="i in page_index_range(page+1, max_page)" v-bind:key="key">
                                    <a class="page-link" v-bind:href="switch_page(i)">{{i}}</a>
                                </li>
                            </template>
                            <template v-else>
                                <template v-if="page < 7">
                                    <li class="page-item" v-for="i in page_index_range(1, page-1)" v-bind:key="key">
                                        <a class="page-link" v-bind:href="switch_page(i)">{{i}}</a>
                                    </li>
                                    <li class="page-item active" aria-current="page">
                                        <span class="page-link">{{page}}</span>
                                    </li>
                                    <li class="page-item" v-for="i in page_index_range(page+1, page+3)"
                                        v-bind:key="key">
                                        <a class="page-link" v-bind:href="switch_page(i)">{{i}}</a>
                                    </li>
                                    <li class="page-item disabled" aria-disabled="true"><span
                                            class="page-link">...</span>
                                    </li>
                                    <li class="page-item" v-for="i in page_index_range(max_page-2, max_page)"
                                        v-bind:key="key">
                                        <a class="page-link" v-bind:href="switch_page(i)">{{i}}</a>
                                    </li>
                                </template>
                                <template v-else-if="page >= max_page - 5">
                                    <li class="page-item" v-for="i in 3" v-bind:key="key">
                                        <a class="page-link" v-bind:href="switch_page(i)">{{i}}</a>
                                    </li>
                                    <li class="page-item disabled" aria-disabled="true"><span
                                            class="page-link">...</span>
                                    </li>
                                    <li class="page-item" v-for="i in page_index_range(page-3, page-1)"
                                        v-bind:key="key">
                                        <a class="page-link" v-bind:href="switch_page(i)">{{i}}</a>
                                    </li>
                                    <li class="page-item active" aria-current="page">
                                        <span class="page-link">{{page}}</span>
                                    </li>
                                    <li class="page-item" v-for="i in page_index_range(page+1, max_page)"
                                        v-bind:key="key">
                                        <a class="page-link" v-bind:href="switch_page(i)">{{i}}</a>
                                    </li>
                                </template>
                                <template v-else>
                                    <li class="page-item" v-for="i in 3" v-bind:key="key">
                                        <a class="page-link" v-bind:href="switch_page(i)">{{i}}</a>
                                    </li>
                                    <li class="page-item disabled" aria-disabled="true"><span
                                            class="page-link">...</span>
                                    </li>
                                    <li class="page-item" v-for="i in page_index_range(page-3, page-1)"
                                        v-bind:key="key">
                                        <a class="page-link" v-bind:href="switch_page(i)">{{i}}</a>
                                    </li>
                                    <li class="page-item active" aria-current="page">
                                        <span class="page-link">{{page}}</span>
                                    </li>
                                    <li class="page-item" v-for="i in page_index_range(page+1, page+3)"
                                        v-bind:key="key">
                                        <a class="page-link" v-bind:href="switch_page(i)">{{i}}</a>
                                    </li>
                                    <li class="page-item disabled" aria-disabled="true"><span
                                            class="page-link">...</span>
                                    </li>
                                    <li class="page-item" v-for="i in page_index_range(max_page-2, max_page)"
                                        v-bind:key="key">
                                        <a class="page-link" v-bind:href="switch_page(i)">{{i}}</a>
                                    </li>
                                </template>
                            </template>
                            <li v-bind:class="{'page-item':true, 'disable': page >= max_page}">
                                <a class="page-link cm-navi"
                                   v-bind:href="page < max_page? switch_page(page+1): 'javascript:void(0)'">
                                    <i style="line-height: 18px;" v-bind:style="{'color': page >= max_page? 'grey': ''}"
                                       class="material-icons prefix">chevron_right</i>
                                </a>
                            </li>
                        </ul>
                    </nav>
                </div>
                <div class="col-sm-12 col-lg-3">
                    <paper-card id="category-pannel" class="animated bounceInRight">
                        <div>
                            <p style="display: inline-block;white-space: pre">Category{{" "}}</p>
                            <a style="cursor: pointer" role="button" id="categoryContent" data-toggle="dropdown"
                               aria-haspopup="true" aria-expanded="false">
                                <i class="fa fa-bars" aria-hidden="true"></i>
                            </a>
                            <div id="category-menu" class="dropdown-menu" style="max-height: 350px; overflow-y: scroll;"
                                 aria-labelledby="categoryContent">
                                <a v-for="(saved_category,key) in category" v-bind:key="key" class="dropdown-item"
                                   @click="switch_category(key)">{{saved_category.main.name}}</a>
                            </div>
                        </div>
                        <template v-for="(saved_category,key) in category">
                            <div v-bind:key="key" v-if="page_setting.category_page == key">
                                <template v-if="saved_category.root.length >= 1">
                                    <span class="badge badge-title badge-tag selected ">基本</span>
                                    <div style="clear:both"></div>
                                    <span v-for="saved_category_root in saved_category.root" v-bind:key="key"
                                          @click="choose_parm('category', page_setting.category==saved_category_root.cateId? null:saved_category_root.cateId)"
                                          v-bind:class="{'badge':true, 'badge-tag':true, 'selected':getUrlKey('category')==saved_category_root.cateId}">{{saved_category_root.name}}</span>
                                    <div style="clear:both"></div>
                                </template>
                                <template v-if="saved_category.noroot.length >= 1">
                                    <template v-for="saved_category_noroot in saved_category.noroot">
                                        <span class="badge badge-tag badge-title selected " v-bind:key="key">{{saved_category_noroot.main.name}}</span>
                                        <div style="clear:both" v-bind:key="key"></div>
                                        <span v-for="saved_category_noroot_root in saved_category_noroot.root"
                                              v-bind:key="key1"
                                              @click="choose_parm('category', page_setting.category==saved_category_noroot_root.cateId? null:saved_category_noroot_root.cateId)"
                                              v-bind:class="{'badge':true, 'badge-tag':true, 'selected': getUrlKey('category')==saved_category_noroot_root.cateId}">{{saved_category_noroot_root.name}}</span>
                                        <div style="clear:both" v-bind:key="key"></div>
                                    </template>
                                </template>
                            </div>
                        </template>
                    </paper-card>
                    <paper-card id="source-pannel" class="animated bounceInRight">
                        <div>
                            <p style="display: inline-block;white-space: pre">Source{{" "}}</p>
                            <a style="cursor: pointer" role="button" id="sourceContent" data-toggle="dropdown"
                               aria-haspopup="true" aria-expanded="false">
                                <i class="fa fa-bars" aria-hidden="true"></i>
                            </a>
                            <div id="source-menu" class="dropdown-menu" style="max-height: 350px; overflow-y: scroll;"
                                 aria-labelledby="sourceContent">
                                <a v-for="(saved_source,key) in source" v-bind:key="key" class="dropdown-item"
                                   @click="switch_source(key)">{{saved_source.main.name}}</a>
                            </div>
                        </div>
                        <template v-for="(saved_source,key) in source">
                            <div v-bind:key="key" v-if="page_setting.source_page == key">
                                <template v-if="saved_source.root.length >= 1">
                                    <span class="badge badge-title badge-tag selected ">基本</span>
                                    <div style="clear:both"></div>
                                    <span v-for="saved_source_root in saved_source.root" v-bind:key="key"
                                          @click="choose_parm('source', page_setting.source==saved_source_root.sourceId? null:saved_source_root.sourceId)"
                                          v-bind:class="{'badge':true, 'badge-tag':true, 'selected': getUrlKey('source')==saved_source_root.sourceId}">{{saved_source_root.name}}</span>
                                    <div style="clear:both"></div>
                                </template>
                                <template v-if="saved_source.noroot.length >= 1">
                                    <template v-for="saved_source_noroot in saved_source.noroot">
                                        <span class="badge badge-tag badge-title selected " v-bind:key="key">{{saved_source_noroot.main.name}}</span>
                                        <div style="clear:both" v-bind:key="key"></div>
                                        <span v-for="saved_source_noroot_root in saved_source_noroot.root"
                                              v-bind:key="key1"
                                              @click="choose_parm('source', page_setting.source==saved_source_noroot_root.sourceId? null:saved_source_noroot_root.sourceId)"
                                              v-bind:class="{'badge':true, 'badge-tag':true, 'selected': getUrlKey('source')==saved_source_noroot_root.sourceId}">{{saved_source_noroot_root.name}}</span>
                                        <div style="clear:both" v-bind:key="key"></div>
                                    </template>
                                </template>
                            </div>
                        </template>
                    </paper-card>
                </div>
            </div>
        </div>
        <footers></footers>
    </div>
</template>

<script>
    /* eslint no-useless-escape: "off"*/
    /* eslint vue/valid-v-for: "off"*/
    import navBar from "@/components/navbar/navbar";
    import fun from '@/assets/js/OJFunction'
    import Footers from "@/components/footer/footer";
    import Loadings from "../../components/loading/loading";
    // import axios from "axios";

    export default {
        components: {
            Loadings,
            Footers,
            'navbar': navBar
        },
        name: "problemset",
        data() {
            return {
                page_belong: 'problemset',
                page_setting: {
                    access_token: "",
                    refresh_token: "",
                    language: "",
                    page: null,
                    category_page: 0,
                    source_page: 0,
                    source: null,
                    category: null,
                    title: null
                },
                max_page: 0,
                page: 0,
                problems: null,
                category: "",
                source: "",
                search: {
                    problem_id: null,
                    title: null
                },
            }
        },
        methods: {
            page_index_range: function (a, b) {
                var res = [];
                for (var i = a; i <= b; i++) {
                    res.push(i);
                }
                return res;
            },
            switch_page: function (p) {
                this.addUrlParm("page", p);
                var url = this.GLOBAL.OJ_DEFAULT_FRONT_SERVE + "/problemset.html" + this.getUrlString();
                return url;
            },
            choose_problem_id: function () {
                var pid = this.search.problem_id;
                window.location.href = this.GLOBAL.OJ_DEFAULT_FRONT_SERVE + '/problem.html?pid=' + encodeURIComponent(pid);
            },
            choose_title: function (e) {
                this.addUrlParm("title", this.search.title);
                var url = this.GLOBAL.OJ_DEFAULT_FRONT_SERVE + "/problemset.html" + this.getUrlString();
                window.location.href = url;
                e.preventDefault();
            },
            getUrlKey: function (key) {
                return fun.getUrlKey(key);
            },
            addUrlParm: function (key, value) {
                // var urlstring = "";
                if (fun.getUrlKey("source") != null) this.page_setting.source = fun.getUrlKey("source");
                if (fun.getUrlKey("category") != null) this.page_setting.category = fun.getUrlKey("category");
                if (fun.getUrlKey("title") != null) this.page_setting.title = fun.getUrlKey("title");
                if (fun.getUrlKey("page") != null) this.page_setting.page = fun.getUrlKey("page");

                if ("source" == key) this.page_setting.source = value != null ? value : null;
                if ("category" == key) this.page_setting.category = value != null ? value : null;
                if ("title" == key) this.page_setting.title = value != null && value != "" ? value : null;
                if ("page" == key) this.page_setting.page = value != null && value != "" ? value : null;
            },
            getUrlString: function () {
                var str = "";
                if (this.page_setting.page != null) str += "&page=" + encodeURIComponent(this.page_setting.page);
                if (this.page_setting.source != null) str += "&source=" + encodeURIComponent(this.page_setting.source);
                if (this.page_setting.category != null) str += "&category=" + encodeURIComponent(this.page_setting.category);
                if (this.page_setting.title != null) str += "&title=" + encodeURIComponent(this.page_setting.title);
                return str.replace("&", "?");
            },
            switch_category: function (num) {
                this.page_setting.category_page = num;
            },
            switch_source: function (num) {
                this.page_setting.source_page = num;
            },
            choose_parm: function (key, val) {
                this.$refs.load.loading_show();
                this.addUrlParm(key, val);
                var url = this.GLOBAL.OJ_DEFAULT_FRONT_SERVE + "/problemset.html" + this.getUrlString();
                window.location.href = url;
            }
        },
        created() {
            this.page_setting.access_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token");
            this.page_setting.refresh_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token");
            // alert(this.page_setting.access_token)
            // console.log(this.page_setting.access_token);
            var lang = localStorage.getItem(this.GLOBAL.OJ_DEFAULT_NAME + "lang");
            if (lang != null) {
                this.page_setting.language = lang;
            }

            this.page_setting.page = fun.getUrlKey("page");
            this.page_setting.source = fun.getUrlKey("source");
            this.page_setting.category = fun.getUrlKey("category");
            this.page_setting.title = this.search.title = fun.getUrlKey("title");
            this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/problemset',
                {
                    language: this.page_setting.language,
                    page: this.page_setting.page,
                    source: this.page_setting.source,
                    category: this.page_setting.category,
                    title: this.page_setting.title
                }, {
                    emulateJSON: true,
                    headers: {
                        OJ_ACCESS_TOKEN: this.page_setting.access_token,
                        OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                    }
                }).then(function (res) {
                // console.log(res);
                console.log("yes2");
                this.category = res.body.OJ_CATEGORY;
                this.problems = res.body.OJ_PROBLEM;
                this.source = res.body.OJ_SOURCE;
                this.page = res.body.OJ_PAGE_INDEX;
                this.max_page = res.body.OJ_MAX_PAGE_INDEX;
                // this.page_setting.access_token = res.body.OJ_TOKEN;
                console.log(res);
                var error_type = res.body.OJ_ERROR_TYPE;
                // console.log(error_type == this.GLOBAL.OJ_ERROR_NO_ERROR);
                if (error_type == this.GLOBAL.OJ_ERROR_NO_ERROR) {
                    var remember_me = res.body.OJ_USER_INFO.saved;
                    var access_token = res.headers.map['oj_access_token'][0];
                    var refresh_token = res.headers.map['oj_refresh_token'][0];
                    if (res.body.OJ_USER_INFO.role.indexOf(this.GLOBAL.OJ_ROLE_ADMIN) || res.body.OJ_USER_INFO.role.indexOf(this.GLOBAL.OJ_ROLE_SUPER_ADMIN) ||
                        res.body.OJ_USER_INFO.role.indexOf(this.GLOBAL.OJ_ROLE_PROBLEM_ADMIN)) {
                        fun.init_account_controller(this.GLOBAL.OJ_GROUP_NORMAL_ADMIN);
                    } else {
                        fun.init_account_controller(this.GLOBAL.OJ_GROUP_NORMAL_USER);
                    }

                    if (remember_me) {
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token, 7);
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token, 7);
                    } else {
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token);
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token);
                    }
                } else if (error_type == this.GLOBAL.OJ_ERROR_NOT_LOGIN) {
                    fun.init_account_controller(this.GLOBAL.OJ_GROUP_NORMAL_VISITOR);
                }

                lang = res.body.OJ_LANGUAGE_TYPE;
                localStorage.setItem(this.GLOBAL.OJ_DEFAULT_NAME + "lang", lang);
                this.$i18n.locale = lang;
                // alert(this.max_page);
            });
        }
    }
</script>

<style scoped lang="scss">

    #problemset {
        overflow-x: hidden;

        empty-container {
            display: block;
            text-align: center;
            margin-bottom: 4rem;
            margin-top: 4rem;

            p {
                margin-left: 20px;
                display: inline-block;
                font-size: 20px;
                font-weight: bold;
                font-family: "Roboto-slab";
            }
        }

        #source-pannel {
            .badge-tag {
                white-space: pre-line;
                text-align: unset;
                line-height: 15px;
                color: #6c757d;
                margin-right: 3px;
                background-color: transparent;
                /* max-width: 7rem; */
                overflow: hidden;
                text-overflow: ellipsis;
                border: 1px solid #6c757d;
                cursor: pointer;
            }

            .badge-tag.selected {
                color: white;
                background-color: #6c757d;
            }

            .badge-title:not(:first-of-type) {
                margin-top: 20px;
            }
        }

        #category-pannel {
            .badge-tag {
                white-space: pre-line;
                text-align: unset;
                line-height: 15px;
                color: #03a9f4;
                margin-right: 3px;
                background-color: transparent;
                /* max-width: 7rem; */
                overflow: hidden;
                text-overflow: ellipsis;
                border: 1px solid #03a9f4;
                cursor: pointer;
            }

            .badge-tag.selected {
                color: white;
                background-color: #03a9f4;
            }

            .badge-title:not(:first-of-type) {
                margin-top: 20px;
            }
        }

        #category-menu {
            max-width: 30rem;
            min-width: 14rem;
        }

        #category-menu::-webkit-scrollbar, #source-menu::-webkit-scrollbar {
            width: 5px;
            height: 16px;
            background-color: #F5F5F5;
            border-radius: 4px;
